body {
  width: 100%;
  height: 100%;
}

.SignUpInput.error {
  border: 1px solid red;
}

.SignUpInput.success {
  border: 1px solid greenyellow;
}

.SignUpPage {
  width: 100%;
  height: 100vh;
  background-color: white;
  display: grid;
  grid-template-columns: 25% 75%;
  overflow: hidden;
}

.SignUpDetails {
  margin-left: auto;
  margin-right: auto;
  width: 80%;
  height: 98vh;
}

.ant-row {
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.SignUpHeading {
  color: #666666;
  margin-bottom: 30%;
  margin-top: 20%;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  font-family: "Roboto", sans-serif;
  font-size: 36px;
}

.SignUpInput {
  border: none;
  border-radius: 0;
  border-bottom: 1px solid #d9d9d9;
  background-color: white;
}

.SignUpLabel {
  margin-bottom: -10px;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  color: #666666;
}

.ant-btn {
  margin-top: 5%;
  border-radius: 0;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  color: white;
  font-weight: semi-bold;
}

.SignUpLink {
  margin-top: -5px;
  font-family: "Roboto ", sans-serif;
  font-size: 14px;
  color: #0075e1;
  font-weight: semi-bold;
}

.copyright {
  position: absolute;
  bottom: 0;
  font-size: 13px;
}
