body {
  width: 100%;
  height: 100%;
}

.OTPInput.error {
  border: 1px solid red;
}

.OTPInput.success {
  border: 1px solid greenyellow;
}

.OTPPage {
  width: 100%;
  height: 100vh;
  background-color: white;
  display: grid;
  grid-template-columns: 25% 75%;
}

.OTPDetails {
  margin-left: auto;
  margin-right: auto;
  width: 80%;
  height: 98vh;
}

.otpResendLink {
  margin-top: 0;
  text-decoration: underline;
  font-family: "Roboto", sans-serif;
  font-size: 14px;
  color: #0075e1;
  margin-bottom: 0;
  font-weight: 500;
}

.otpResendLink:hover {
  color: rgba(0, 117, 225, 0.7);
}

.ant-row {
  height: 20px;
  display: flex;
}

.OTPHeading {
  color: #666666;
  margin-top: 10%;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  font-family: "Roboto", sans-serif;
  font-size: 36px;
}

.OTPInput {
  border: none;
  border-radius: 0;
  border-bottom: 1px solid #d9d9d9;
  background-color: white;
}

.LoginLabel {
  font-family: "Roboto", sans-serif;
  font-size: 24px;
  color: #666666;
}

.ant-btn {
  border-radius: 0;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  color: white;
  font-weight: semi-bold;
}

.copyright {
  position: absolute;
  bottom: 10;
  font-size: 13px;
}

.ForgetPasswordSubTitle {
  font-family: "Roboto", sans-serif;
  font-size: 18px;
  color: #5a5a5a;
  text-align: center;
  font-weight: 100;
}

.OTPText {
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  color: #666666;
  text-align: center;
  font-weight: 100;
}

.emailIcon {
  width: 50px;
  height: 50px;
}
