body {
  height: 100vh;
  width: 100vw;
  overflow-x: hidden;
}

.search {
  display: flex;
  flex-direction: row;
  gap: 15px;
  width: 100%;
}

.tableContainer {
  width: 100%;
  margin-top: 0px;
}

:where(.css-dev-only-do-not-override-qgg3xn).ant-table-wrapper
  .ant-table-thead
  > tr
  > th,
:where(.css-dev-only-do-not-override-qgg3xn).ant-table-wrapper
  .ant-table-thead
  > tr
  > td {
  background-color: #15295e;
  color: #fff;
  font-weight: 500;
}

.ant-table-row .ant-table-row-level-0 {
  height: 10px !important ;
}

.tableContainer th {
  position: sticky;
  top: 0;
  background: white;
  z-index: 10;
}

.tableContainer tbody {
  display: block;
  height: 400px;
  overflow: auto;
}

.tableContainer thead,
.tableContainer tr {
  display: table;
  width: 100%;
  table-layout: fixed;
}

.search {
  display: flex;
  flex-direction: row;
  gap: 25px;
  width: 100%;
}

.searchSub {
  display: flex;
  flex-direction: row;
  gap: 25px;
  width: 100%;
}

.searchInputDate {
  width: 100% !important;
  display: flex;
  flex-direction: row;
  gap: 15px;
}

.center-content {
  display: flex;
  justify-content: center;
  align-items: center;
}

.assignBtn:disabled {
  background-color: green;
  color: white;
}

.assignBtn {
  background-color: green;
  color: white;
  height: 30px;
  cursor: pointer;
}

.assignBtnDelete {
  background-color: red;
  color: white;
  margin: 10px;
  height: 30px;
  cursor: pointer;
}
